import { TreeItem, Registry, Color } from '@zeainc/zea-engine'
import { CADAsset } from './CADAsset.js'
import { CADPart } from './CADPart.js'

const findCADAsset = (treeItem) => {
  while (!(treeItem instanceof CADAsset)) treeItem = treeItem.getOwner()
  return treeItem
}
const traverse = (treeItem, callback) => {
  const __c = (treeItem, depth) => {
    const children = treeItem.getChildren()
    for (const childItem of children) {
      if (childItem) __t(childItem, depth + 1)
    }
  }

  const __t = (treeItem, depth) => {
    if (callback(treeItem, depth) != false) {
      __c(treeItem, depth)
    }
  }
  __t(treeItem, 1)
}

const highlightColor = new Color('#F9CE03')
highlightColor.a = 0.1

/**
 * Represents a view of PMI data. within a CAD assembly.
 *
 * @extends TreeItem
 */
class PMIItem extends TreeItem {
  /**
   * Creates an instance of PMIItem setting up the initial configuration for Material and Color parameters.
   *
   * @param {string} name - The name value.
   */
  constructor(name) {
    super(name)
  }

  /**
   * The clone method constructs a new PMIItem, copies its values
   * from this item and returns it.
   *
   * @param {number} flags - The flags param.
   * @return {PMIItem} - The return value.
   */
  clone(flags) {
    const cloned = new PMIItem()
    cloned.copyFrom(this, flags)
    return cloned
  }

  /**
   * Changes the current state of the selection of this item.
   *
   * @emits `selectedChanged` with selected state
   * @param {boolean} sel - Boolean indicating the new selection state.
   */
  setSelected(sel) {
    super.setSelected(sel)
    if (sel) this.activate()
    else this.deactivate()
  }

  /**
   * Activates the PMIItem, adjusting visibility of the PMI items and the camera Xfo
   *
   * @param {Camera} camera - Boolean indicating the new selection state.
   */
  activate() {
    console.log('activate')
    this.getParameters().forEach((param) => {
      const name = param.getName()
      if (name != 'Visible' && name != 'GlobalXfo' && name != 'LocalXfo' && name != 'BoundingBox') console.log(name)
    })
    const cadAsset = findCADAsset(this)
    // const cadPart = findCADPart(this)
    const pmiContainer = this.getOwner().getOwner()
    const pmiOwner = pmiContainer.getOwner()
    if (pmiOwner) {
      const linkedEntitiesParam = this.getParameter('LinkedEntities')
      if (linkedEntitiesParam) {
        const linkedEntityPaths = linkedEntitiesParam.getValue()
        console.log(linkedEntityPaths)

        linkedEntityPaths.forEach((pathStr) => {
          const path = pathStr.split(', ')
          try {
            const linkedEntity = cadAsset.resolvePath(path)
            if (linkedEntity) {
              linkedEntity.addHighlight('pmi', highlightColor, true)
            }
          } catch (e) {
            console.log(e.message)
            // traverse(pmiOwner, (treeItem) => {
            //   if (treeItem.getName() == 'PMI') return false
            //   console.log(treeItem.getName())
            //   if (treeItem.getName() == pathStr) {
            //     linkedEntity.addHighlight('pmi', highlightColor, true)
            //     return false
            //   }
            // })
          }
        })
      }
    }
  }
  deactivate() {
    console.log('deactivate')
    const cadAsset = findCADAsset(this)
    // const cadPart = findCADPart(this)
    const pmiOwner = this.getOwner().getOwner()
    if (pmiOwner) {
      const linkedEntitiesParam = this.getParameter('LinkedEntities')
      if (linkedEntitiesParam) {
        const linkedEntityPaths = linkedEntitiesParam.getValue()
        console.log(linkedEntityPaths)

        linkedEntityPaths.forEach((pathStr) => {
          const path = pathStr.split(', ')
          try {
            const linkedEntity = cadAsset.resolvePath(path)
            if (linkedEntity) {
              linkedEntity.removeHighlight('pmi', true)
            }
          } catch (e) {}
        })
      }
    }
  }
}

Registry.register('PMIItem', PMIItem)

export { PMIItem }
